* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-top: 40px;
  margin-left: 40px;
}
.country-list {
  list-style: none;
  margin-left: 40px;
}
.country-item {
  display: flex;
  align-items: center;
}
.country-img {
  margin-right: 10px;
}
.country-info {
  margin-left: 40px;
}
.data {
  display: flex;
}
.bold {
  font-weight: 700;
}
